import { createClient } from '@supabase/supabase-js';

// In development, we'll use mock values
const isDevelopment = process.env.NODE_ENV === 'development';

if (!isDevelopment && !process.env.NEXT_PUBLIC_SUPABASE_URL) {
  throw new Error('Missing NEXT_PUBLIC_SUPABASE_URL environment variable');
}

if (!isDevelopment && !process.env.SUPABASE_SERVICE_KEY) {
  throw new Error('Missing SUPABASE_SERVICE_KEY environment variable');
}

// Create a mock client for development
const mockClient = {
  auth: {
    getUser: async (token: string) => {
      if (token === 'mock-admin-token') {
        return {
          data: {
            user: {
              id: 'mock-admin-id',
              email: process.env.MOCK_ADMIN_EMAIL,
              role: 'admin',
            }
          },
          error: null
        };
      }
      return { data: { user: null }, error: new Error('Invalid token') };
    },
    signIn: async ({ email, password }: { email: string; password: string }) => {
      if (
        email === process.env.MOCK_ADMIN_EMAIL &&
        password === process.env.MOCK_ADMIN_PASSWORD
      ) {
        return {
          data: {
            session: {
              access_token: 'mock-admin-token',
              user: {
                id: 'mock-admin-id',
                email: process.env.MOCK_ADMIN_EMAIL,
                role: 'admin',
              }
            }
          },
          error: null
        };
      }
      return { data: null, error: new Error('Invalid credentials') };
    },
    signOut: async () => {
      return { error: null };
    }
  },
  from: (table: string) => ({
    select: () => ({
      eq: () => ({
        single: async () => {
          if (table === 'users') {
            return {
              data: {
                role: 'admin',
                status: 'active'
              },
              error: null
            };
          }
          return { data: null, error: null };
        }
      })
    })
  })
};

// Use mock client in development, real client in production
export const supabaseAdmin = isDevelopment
  ? mockClient
  : createClient(
      process.env.NEXT_PUBLIC_SUPABASE_URL!,
      process.env.SUPABASE_SERVICE_KEY!,
      {
        auth: {
          autoRefreshToken: false,
          persistSession: false,
        },
      }
    );

export const supabaseClient = isDevelopment
  ? mockClient
  : createClient(
      process.env.NEXT_PUBLIC_SUPABASE_URL!,
      process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY!
    );
